    background-color: darkkhaki;
    position: fixed;
    top: 0;
    width:100vw;
    font-family: 'Chilanka';

}
ul{
 display: flex;
 justify-content:flex-start;
 flex-wrap: wrap;
 color: azure;
}
li{
    margin-right: 50px;
    list-style-type: none;
    color: azure;
}
